<template>
  <div>
    <el-container>
      <el-header style="padding: 0px;display:flex;justify-content:space-between;align-items: center">
        <div style="display: inline">
          <el-input
            placeholder="通过病人姓名搜索病人信息,记得回车哦."
            clearable
            @change="keywordsChange"
            style="width: 300px;margin: 0px;padding: 0px;"
            size="mini"
            :disabled="advanceSearchViewVisible"
            @keyup.enter.native="searchEmp"
            prefix-icon="el-icon-search"
            v-model="roleName">
          </el-input>
          <el-button type="primary" size="mini" style="margin-left: 5px" icon="el-icon-search" @click="searchEmp">搜索
          </el-button>
        </div>
        <div style="margin-left: 5px;margin-right: 20px;display: inline">
          <el-button type="primary" size="mini" icon="el-icon-plus"
                     @click="showAddEmpView">
            添加病人
          </el-button>
        </div>
      </el-header>
      <el-main style="padding-left: 0px;padding-top: 0px">
        <div>
          <el-table
            :data="patientList"
            v-loading="tableLoading"
            border
            size="mini"
            style="width: 100%">
            <el-table-column
              prop="code"
              align="center"
              label="病人编号"
            >
            </el-table-column>
            <el-table-column
              prop="name"
              align="center"
              label="姓名">
            </el-table-column>
            <el-table-column
              prop="idCard"
              align="center"
              label="身份证">
            </el-table-column>
            <el-table-column
              prop="sex"
              align="center"
              label="性别"
            >
            </el-table-column>
            <el-table-column
              prop="balance"
              align="center"
              label="余额"
            >
            </el-table-column>
            <el-table-column
              prop="phone"
              align="center"
              label="联系方式">
            </el-table-column>
            <el-table-column
              align="center"
              label="操作"
              width="280">
              <template slot-scope="scope">
                <el-button @click="showEditEmpView(scope.row)" style="padding: 3px 20px 3px 20px;margin: 2px" size="small">编辑
                </el-button>
                <el-button  @click="transaView(scope.row)"   type="primary" style="padding: 3px 10px 3px 10px;margin: 2px" size="small">交易记录
                </el-button>
                <el-button  @click="arrearageView(scope.row)"   type="danger" style="padding: 3px 10px 3px 10px;margin: 2px" size="small">欠费记录
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          <div style="display: flex;justify-content: space-between;margin: 2px;float: right">
            <el-pagination
              background
              :page-size="10"
              :current-page="currentPage"
              @current-change="currentChange"
              layout="prev, pager, next"
              :total="totalCount">
            </el-pagination>
          </div>
        </div>
      </el-main>
    </el-container>


    <el-form :model="Ipatient" :rules="rules" ref="addEmpForm" style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="InsdialogVisible"  width="65%">
          <el-row>
            <el-col :span="8">
              <el-form-item label="姓 名:" prop="name">
                <el-input prefix-icon="el-icon-edit" v-model="Ipatient.name"  size="mini" style="width: 150px"
                          placeholder="请输入姓名"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="性   别:" prop="sex">
                 <el-select  v-model="Ipatient.sex" size="mini"  placeholder="请选择性别" style="width:150px">
                    <el-option  v-for="item in sexData" :key="item.sex" :label="item.mame" :value="item.sex"> </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="身 份 证:" prop="idcard">
                <el-input prefix-icon="el-icon-edit" v-model="Ipatient.idCard"  size="mini" style="width: 150px"
                          placeholder="请输入身份证"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <el-form-item label="家属姓名:" prop="linkman">
                <el-input prefix-icon="el-icon-edit" v-model="Ipatient.linkman"  size="mini" style="width: 150px"
                          placeholder="请输入家属姓名"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="联系方式:" prop="phone">
                <el-input prefix-icon="el-icon-edit" v-model="Ipatient.phone"  size="mini" style="width: 150px"
                          placeholder="请输入联系方式"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="联系地址:" prop="address">
                <el-input prefix-icon="el-icon-edit" v-model="Ipatient.address"  size="mini" style="width: 150px"
                          placeholder="请输入联系地址"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <span slot="footer" class="dialog-footer">
               <el-button size="mini" @click="cancelEidt">取 消</el-button>
              <el-button size="mini" type="primary" @click="addEmp('addEmpForm')">确 定</el-button>
               </span>
        </el-dialog>
      </div>
    </el-form>


    <el-form :model="patient" :rules="rules" ref="addEmpForm" style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="dialogVisible"  width="65%">
          <el-row>
            <el-col :span="8">
              <el-form-item label="病人编号:" prop="code">
                <el-input prefix-icon="el-icon-edit" v-model="patient.code" disabled="disabled"  size="mini" style="width: 150px"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="姓 名:" prop="name">
                <el-input prefix-icon="el-icon-edit" v-model="patient.name"  size="mini" style="width: 150px"
                          placeholder="请输入姓名"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="性   别:" prop="sex">
                <el-select  v-model="patient.sex" size="mini"  placeholder="请选择性别" style="width:150px">
                  <el-option  v-for="item in sexData" :key="item.sex" :label="item.mame" :value="item.sex"> </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <el-form-item label="身 份 证:" prop="idcard">
                <el-input prefix-icon="el-icon-edit" v-model="patient.idcard"  size="mini" style="width: 150px"
                          placeholder="请输入身份证"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="家属姓名:" prop="linkman">
                <el-input prefix-icon="el-icon-edit" v-model="patient.linkman"  size="mini" style="width: 150px"
                          placeholder="请输入家属姓名"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="联系方式:" prop="phone">
                <el-input prefix-icon="el-icon-edit" v-model="patient.phone"  size="mini" style="width: 150px"
                          placeholder="请输入联系方式"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <el-form-item label="联系地址:" prop="address">
                <el-input prefix-icon="el-icon-edit" v-model="patient.address"  size="mini" style="width: 150px"
                          placeholder="请输入联系地址"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <span slot="footer" class="dialog-footer">
               <el-button size="mini" @click="cancelEidt">取 消</el-button>
              <el-button size="mini" type="primary" @click="modifyClick()">确 定</el-button>
               </span>
        </el-dialog>
      </div>
    </el-form>

    <el-form style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="transaVisible"  width="65%">
          <el-row>
            <el-col :span="8">
              <el-form-item label="交易日期:">
                <div class="block">
                  <el-date-picker
                    v-model="startDateAndEnd"
                    type="daterange"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期"
                    value-format="yyyy-MM-dd"
                    size="mini"
                    style="width:200px" >
                  </el-date-picker>
                </div>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item  >
                <el-button size="mini"  type="primary" @click="getTransa">查询</el-button>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
             <el-table
               :data="transactionList"
               v-loading="tableLoading"
               border
               size="mini"
               style="width: 100%">
               <el-table-column
                 prop="code"
                 align="center"
                 label="交易单据号">
               </el-table-column>
               <el-table-column
                 prop="dateTime"
                 align="center"
                 label="交易日期">
               </el-table-column>
               <el-table-column
                 prop="money"
                 align="center"
                 label="交易金额">
               </el-table-column>
               <el-table-column
                 prop="type"
                 align="center"
                 label="交易方式">
               </el-table-column>
             </el-table>
             <div style="display: flex;justify-content: space-between;margin: 2px;float: right">
               <el-pagination
                 background
                 :page-size="10"
                 :current-page="InscurrentPage"
                 @current-change="InsCountChange"
                 layout="prev, pager, next"
                 :total="InsCount">
               </el-pagination>
             </div>
          </el-row>
          <span slot="footer" class="dialog-footer">
              <el-button size="mini" type="primary" @click="cancelEidt()">关 闭</el-button>
          </span>
        </el-dialog>
      </div>
    </el-form>

    <el-form style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="arrearageVisible"  width="65%">
        <!--  <el-row>
            <el-col :span="8">
              <el-form-item label="交易日期:">
                <div class="block">
                  <el-date-picker
                    v-model="startDateAndEnd"
                    type="daterange"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期"
                    value-format="yyyy-MM-dd"
                    size="mini"
                    style="width:200px" >
                  </el-date-picker>
                </div>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item  >
                <el-button size="mini"  type="primary" @click="getTransa">查询</el-button>
              </el-form-item>
            </el-col>
          </el-row>-->
          <el-row>
            <el-table
              :data="arrearageList"
              v-loading="tableLoading"
              border
              size="mini"
              style="width: 100%">
              <el-table-column
                prop="recordCode"
                align="center"
                label="单据号">
              </el-table-column>
              <el-table-column
                prop="date"
                align="center"
                label="欠费日期">
              </el-table-column>
              <el-table-column
                prop="hospitalName"
                align="center"
                label="单位">
              </el-table-column>
              <el-table-column
                prop="money"
                align="center"
                label="欠费金额">
              </el-table-column>
            </el-table>
            <div style="display: flex;justify-content: space-between;margin: 2px;float: right">
              <el-pagination
                background
                :page-size="10"
                :current-page="ArrcurrentPage"
                @current-change="ArrChange"
                layout="prev, pager, next"
                :total="ArrCount">
              </el-pagination>
            </div>
          </el-row>
          <span slot="footer" class="dialog-footer">
              <el-button size="mini" type="primary" @click="cancelEidt()">关 闭</el-button>
          </span>
        </el-dialog>
      </div>
    </el-form>


  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    data() {
      return {
        patientList:[],
        transactionList:[],
        arrearageList:[],
        roleName:'',
        dialogTitle: '',
        resourceSelection:[],
        startDateAndEnd:'',
        totalCount: -1,
        InsCount:-1,
        ArrCount:-1,
        currentPage:1,
        InscurrentPage:1,
        ArrcurrentPage:1,
        dialogVisible: false,
        tableLoading: false,
        InsdialogVisible:false,
        arrearageVisible:false,
        transaVisible:false,
        advanceSearchViewVisible: false,
        patient: {
          code:null,
          name: '',
          idcard: '',
          sex: '',
          phone: '',
          address: '',
          linkman:'',
        },
        Ipatient: {
          name: '',
          idCard: '',
          sex: '',
          phone: '',
          address: '',
          linkman:'',
        },
        patientResource: {
          name: '',
          id:''
        },
        rules: {
          name: [{required: true, message: '必填:姓名', trigger: 'blur'}],
         // idCard: [{required: true, message: '必填:身份 证', trigger: 'blur'}],
       //   sex: [{required: true, message: '必填:性别', trigger: 'blur'}],
          phone: [{required: true, message: '必填:联系方式', trigger: 'blur'}],
        //  address: [{required: true, message: '必填:联系地址', trigger: 'blur'}],
         // linkman: [{required: true, message: '必填:家属信息', trigger: 'blur'}],

        },
        data: [],
        value1:[],
        sexData:[
          {
            sex:'1',
            mame:'男'
          },{
            sex:'2',
            mame:'女'
          }
        ],

      };
    },
    mounted: function () {
      this.loadEmps();
    },
    methods: {
      keywordsChange(val){
        if (val == '') {
          this.loadEmps();
        }
      },
      searchEmp(){
        this.loadEmps();
      },
      //病人分页
      currentChange(currentChange){
        this.currentPage = currentChange;
        this.loadEmps();
      },
      //交易分页
      InsCountChange(InsCountChange){
        this.InscurrentPage = InsCountChange;
        this.getTransa();
      },
      //欠费分页
      ArrChange(ArrChange){
        this.ArrcurrentPage = ArrChange;
        this.getArrearage();
      },
      loadEmps(){
        const _this = this;
        this.patientList = [];
        this.tableLoading = true;
        this.postRequestJson('/baseinfo/patient/list',{
          name: this.roleName,
          curPage:this.currentPage,
          pageSize:10
        }).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
              if(resp.data.code==="200"){
                console.log(resp.data.msg.result);
                let result = resp.data.msg.result;
                for(let i = 0;i<result.length;i++){
                  _this.patientList.push({
                    code:result[i].code,
                    name:result[i].name,
                    idCard:result[i].idCard,
                    sex:result[i].sex == 1?'男':'女',
                    balance:result[i].balance,
                    phone:result[i].phone
                  })
                }
                _this.totalCount = resp.data.msg.totalSize;
              }else{
                this.getMsg(resp.data.msg);
              }
          }else{
            this.getMsg(resp.status);
          }
        })
      },
      //添加病人基础信息
      addEmp(formName){
        var _this = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            //添加
            this.tableLoading = true;
            this.postRequestJson("/baseinfo/patient",this.Ipatient).then(resp=> {
              _this.tableLoading = false;
              if (resp && resp.status === 200) {
                if(resp.data.code==="200"){
                  _this.dialogVisible = false;
                  this.getMsg("添加成功");
                  this.InsdialogVisible = false;
                }else{
                  this.getMsg(resp.data.msg);
                }
              }else{
                this.getMsg(resp.status);
              }
            })
          } else {
            return false;
          }
        });
      },
      //修改
      modifyClick(){
        var _this = this;
        this.tableLoading = true;
        //修改
        this.putRequestJson("/baseinfo/patient",this.patient).then(resp=> {
          _this.tableLoading = false;
          if (resp && resp.status === 200) {
            this.getMsg(resp.data.msg);
            this.dialogVisible = false;
          }else{
            this.getMsg(resp.status);
          }
        })
      },
      //取消按钮
      cancelEidt(){
        this.dialogVisible = false;
        this.InsdialogVisible=false;
        this.transaVisible=false;
        this.arrearageVisible=false;
      },
      //编辑病人基础信息对话框
      showEditEmpView(row){
        this.dialogTitle = "编辑病人基础信息";
        this.patientResource.name = row.code;
        this.dialogVisible = true;
         this.modifyPatent(row.code)

      },
       //病人交易记录对话框
      transaView(row){
         this.dialogTitle = "病人交易记录";
         this.transaVisible=true;
         this.getTransa(row.code)
      },
      //查询交易记录
      getTransa(code){
        this.tableLoading = true;
        this.postRequestJson('/baseinfo/patient/transaction/list',{
          code: code,
          curPage:this.InscurrentPage,
          pageSize:10
        }).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            if(resp.data.code==="200"){
              let data=[];
              let typeNmae="";
              for(let i=0;i<resp.data.msg.result.length;i++){
                if(resp.data.msg.result[i].type==1){
                  typeNmae="充值/续费";
                }else if(resp.data.msg.result[i].type==2){
                  typeNmae="结算";
                }else if(resp.data.msg.result[i].type==3){
                  typeNmae="退款"
                }
                data.push({
                  code:resp.data.msg.result[i].code,
                  dateTime:resp.data.msg.result[i].dateTime,
                  money:resp.data.msg.result[i].money,
                  payMan:resp.data.msg.result[i].payMan,
                  type:typeNmae
                });
              }
              this.transactionList =data;
              this.InsCount = resp.data.msg.totalSize;
            }else{
              this.getMsg(resp.data.msg);
            }
          }else{
            this.getMsg(resp.status);
          }
        })
      },
      //病人欠费记录对话框
      arrearageView(row){
        this.dialogTitle = "病人欠费记录";
        this.arrearageVisible=true;
        this.getArrearage(row.code);
      },
      //查询欠费记录
      getArrearage(code){
        this.tableLoading = true;
        this.postRequestJson('/baseinfo/patient/arrearage/list',{
          code: code,
          curPage:this.ArrcurrentPage,
          pageSize:10
        }).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
             if(resp.data.code==="200"){
               this.arrearageList =resp.data.msg.result;
               this.ArrCount = resp.data.msg.totalSize;
             }else{
               this.getMsg(resp.data.msg);
             }
          }else{
            this.getMsg(resp.status);
          }
        })
      },

      //查询病人详细信息
      modifyPatent(code){
        this.getRequest("/baseinfo/patient?code="+code).then(resp=> {
          if (resp && resp.status === 200) {
            if(resp.data.code==="200"){
              this.patient.code=resp.data.msg.code;
              this.patient.name=resp.data.msg.name;
              // this.patient.sex=resp.data.msg.sex=1?'男':"女";
              this.patient.linkman=resp.data.msg.linkman;
              this.patient.idcard=resp.data.msg.idCard;
              this.patient.address=resp.data.msg.address;
              this.patient.phone=resp.data.msg.phone;
              this.patientResource.id =resp.data.msg.id;
            }else{
              this.getMsg(resp.data.msg);
            }
          }else{
            this.getMsg(resp.status);
          }
        })
      },

      //添加病人基础对话框
      showAddEmpView(){
        this.dialogTitle = "添加病人基础信息";
        this.InsdialogVisible = true;
        this.Ipatient.address="";
        this.Ipatient.idCard="";
        this.Ipatient.linkman="";
        this.Ipatient.name="";
        this.Ipatient.phone="";
        this.Ipatient.sex="";
      },
      getMsg(data){
        this.$confirm( data, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.loadEmps();
        }).catch(() => {
        });
      },

    }
  };
</script>
<style>
  .el-dialog__body {
    padding-top: 0px;
    padding-bottom: 0px;
  }

  .slide-fade-enter-active {
    transition: all .8s ease;
  }

  .slide-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }

  .slide-fade-enter, .slide-fade-leave-to {
    transform: translateX(10px);
    opacity: 0;
  }
</style>



