<script>
  export default {
    data() {
      return {

        searchConditonDTO: {
          name: null,
          orgId: null,
          isOnWork: '',
          curPage: 1,
          pageSize: 10
        },
        isOnWorkOptions:[
          {
            value:1,
            label:'在职'
          },

        ],


        careWorkerList: [],
        roleList: [],
        SelectionChange: [],
        treeData: [],
        sexData: [
          {
            sex: '1',
            mame: '男'
          }, {
            sex: '2',
            mame: '女'
          }
        ],
        jodtitleData: [{
          jodtitle: '1',
          name: "护工"
        }],
        name: '',
        hideRow: false,
        orgId: '',
        dialogTitle: '',
        orgdialogTitle: '',
        totalCount: -1,
        currentPage: 1,
        startDateTime: '',
        dialogVisible: false,
        editDialogVisible: false,
        EdialogVisible: false,
        detailedEmpdialogVisible: false,
        tableLoading: false,
        defaultProps: {
          children: 'children',
          label: 'label',
          id: 'id',
          isLeaf: 'isLeaf',
          disabled: 'disabled'
        },
        orgDataNode: {
          orgId: '',
          orgName: ''
        },
        editDTO: {
          orgId: null,
          orgName: '',
          startDate: null,
          careworkerId: null,
        },
        editOrgTreeVisible: false,
        emp: {
          careworkerId: '',
          code: '',
          name: '',
          idcard: '',
          sex: '',
          phone: '',
          address: '',
          jodtitle: '',
          orgName: '',
          startDate: ''
        },
        insemp: {
          careworkerId: '',
          code: '',
          name: '',
          idcard: '',
          sex: '',
          phone: '',
          address: '',
          jodtitle: ''
        },
        empData: {
          startDateTime: '',
          projectId: '',
          orgId: '',
          employee: ''
        },
        rules: {
          name: [{required: true, message: '必填:姓名', trigger: 'blur'}],
          idcard: [{required: true, message: '必填:身份证', trigger: 'blur'}],
          password: [{required: true, message: '必填:密码', trigger: 'blur'}],
          phone: [{required: true, message: '必填:联系方式', trigger: 'blur'}],
          address: [{required: true, message: '必填:联系地址', trigger: 'blur'}],
          sex: [{required: true, message: '必选:性别', trigger: 'change'}],
          jodtitle: [{required: true, message: '必选:职务', trigger: 'change'}],
        }
      };
    },
    mounted: function () {
      this.loadEmps();
    },
    methods: {

      clearOrgInput() {
        this.orgDataNode.orgName = '';
        this.orgDataNode.orgId = null;
      },


      deleteEmp(row) {
        this.$confirm('此操作将永久删除[' + row.name + '], 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.doDelete(row.careworkerId);
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
        });
      },
      doDelete(ids) {
        this.tableLoading = true;
        var _this = this;
        this.deleteRequest("/busi/careworker?careworkerId=" + ids).then(resp => {
          _this.tableLoading = false;
          if (resp && resp.status === 200) {
            this.$confirm(resp.data.msg, '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {

            }).catch(() => {
            });
            _this.loadEmps();
          } else {
            this.$confirm(resp.data.msg, '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
            }).catch(() => {
            });
          }
        });
      },
      searchEmp() {
        this.loadEmps();
      },
      //分页操作
      currentChange(currentChange) {
        this.currentPage = currentChange;
        this.loadEmps();
      },
      //查询陪护人员信息
      loadEmps() {
        const _this = this;
        this.tableLoading = true;

        this.postRequestJson('/busi/careworker/list', {
          name: this.name,
          curPage: this.currentPage,
          projectId: this.user.projectId,
          orgId: this.orgDataNode.orgId,
          isOnWork:this.searchConditonDTO.isOnWork == 1,
        }).then(resp => {
          console.log(resp);
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            const data = resp.data;
            _this.careWorkerList = resp.data.msg.result;
            _this.totalCount = data.msg.totalSize;
          }
        })
      },
      //添加员工信息
      addEmp(formName) {
        var _this = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            //添加
            this.postRequestJson("/busi/careworker", {
              startDateTime: this.startDateTime,
              projectId: this.user.projectId,
              orgId: this.orgDataNode.orgId,
              employee: this.insemp
            }).then(resp => {
              if (resp && resp.status === 200) {
                if(resp.data.code==="200"){
                  this.getMssage("添加成功");
                }else{
                  this.getMssage(resp.data.msg);
                }
              } else {
                this.getMssage(resp);
              }
            })
          } else {
            return false;
          }
        });
      },
      //修改员工信息
      modifyEmp(formName) {
        var _this = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            //更新
            this.putRequestJson("/busi/careworker", {
              startDateTime: this.editDTO.startDate,
              orgId: this.editDTO.orgId,
              careworkerId: this.editDTO.careworkerId,
            }).then(resp => {
              _this.tableLoading = false;
              if (resp && resp.status == 200) {
                this.getMssage(resp.data.msg);
              } else {
                this.getMssage(resp.data.msg);
              }
            })
          } else {
            return false;
          }
        });
      },
      //取消按钮
      cancelEidt() {
        this.dialogVisible = false;
        this.EdialogVisible = false;
        this.editDialogVisible = false;
        this.editOrgTreeVisible = false;
      },
      //打开修改界面
      showEditEmpView(row) {
        console.log(row);
        this.dialogTitle = "编辑陪护人员信息";
        this.emp.code = row.code;
        this.emp.name = row.name;
        this.emp.orgName = row.orgName;
        this.emp.startDate = row.startDateTime;
        this.editDialogVisible = true;
        this.editDTO.careworkerId = row.careworkerId;
        this.editDTO.orgId = null;
        this.editDTO.startDate = null;
      },
      //打开修改界面的科室树界面
      showEditOrgTable() {
        this.editOrgTreeVisible = true;
      },

      //打开详细界面
      showdetailedEmp(row) {
        this.dialogTitle = "陪护人员详细信息";
        this.detailedEmpdialogVisible = true;
        this.getDetailed(row.careworkerId)
      },
      //查询员工详细信息
      getDetailed(id) {
        this.getRequest("/busi/careworker?careworkerId=" + id).then(resp => {
          console.log(resp);
          if (resp && resp.status === 200) {
            this.emp.code = resp.data.msg.employee.code;
            this.emp.name = resp.data.msg.employee.name;
            this.emp.idcard = resp.data.msg.employee.idcard;
            this.emp.phone = resp.data.msg.employee.phone;
            // this.emp.jodtitle=resp.data.msg.employee.jodtitle === 1?'护工':'其他';
            // this.emp.sex=resp.data.msg.employee.sex === 1?'男':'女';
            this.emp.address = resp.data.msg.employee.address;
            this.orgDataNode.orgName = resp.data.msg.orgName;
            this.startDateTime = resp.data.msg.startDateTime;
          } else {

          }
        })
      },
      //打开添加界面
      showAddEmpView() {
        this.dialogTitle = "添加陪护人员信息";
        this.dialogVisible = true;
        this.insemp.address='';
        this.insemp.code='';
        this.insemp.idcard='';
        this.insemp.jodtitle='';
        this.insemp.sex='';
        this.insemp.phone='';
        this.insemp.name='';
        this.insemp.code='';
        this.insemp.careworkerId='';
        this.orgDataNode.orgName='';
        this.startDateTime='';
      },
      //打开科室参照界面
      showOrgTable() {
        this.orgdialogTitle = "科室参照";
        this.EdialogVisible = true;
      },
      //加载树形界面
      loadChild(node, resolve) {
        let id = (node.parent == null ? null : node.data.id);
        let resolve1 = resolve;
        this.tableLoading = true;
        this.postRequestJson('/busi/baseinfo/organization/list', {
          orgId: id,
          projectId: this.user.projectId,
        }).then(resp => {
          this.tableLoading = false;
          if (resp && resp.status === 200) {

            if(resp.data.code){
              const tdata = [];
              for (let i = 0; i < resp.data.msg.result.length; i++) {
                tdata.push({
                  id: resp.data.msg.result[i].id,
                  label: resp.data.msg.result[i].name,
                  children: [],
                  isLeaf: resp.data.msg.result[i].isLeaf,
                  disabled: !resp.data.msg.result[i].isLeaf
                });
              }
              resolve1(tdata);
            }else{
              this.getMssage(resp.data.msg);
            }
          }else{
            this.getMssage(resp.status);
          }
        });
      },

      orgClick(type) {
        let node = this.$refs.tree.getCheckedNodes(true, false);
        if (type === 'edit') {
          this.editOrgTreeVisible = false;
          this.editDTO.orgName = node[0].label;
          this.editDTO.orgId = node[0].id;
        } else {
          this.EdialogVisible = false;
          this.orgDataNode.orgName = node[0].label;
          this.orgDataNode.orgId = node[0].id;
        }
      },
      orgNodeClick(data, nodes) {
        let target = [];
        target.push(data);
        this.$refs.tree.setCheckedNodes(target, true)
      },
      detailedEmp() {
        this.detailedEmpdialogVisible = false;
      },
      //消息提示框
      getMssage(data) {
        this.$confirm(data, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.dialogVisible = false;
          this.editDialogVisible = false;
          this.loadEmps();
        }).catch(() => {
        });
      },
    },
    computed: {
      user() {
        return this.$store.state.user;
      },
    }

  }
</script>
<style>
  .el-dialog__body {
    padding-top: 0px;
    padding-bottom: 0px;
  }

  .slide-fade-enter-active {
    transition: all .8s ease;
  }

  .slide-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }

  .slide-fade-enter, .slide-fade-leave-to {
    transform: translateX(10px);
    opacity: 0;
  }

  /*.el-tree--highlight-current .el-tree-node.is-current>.el-tree-node__content {
    color: red;
  }*/
</style>



